<template>
  <div id="page-settings">
    <NavBar
      title="设置"
      border
      fixed
      placeholder
      left-arrow
      @click-left="jumpToPreviousPage"
    />
    <VanImage
      id="page-settings-main-icon"
      round
      width="100px"
      height="100px"
      fit="cover"
      :src="require('@/assets/others/default_avatar.png')"
    />
    <h3 id="page-settings-main-nickname">
      {{appUserInfo.nickName}}
    </h3>
    <CellGroup title="基本信息">
      <Cell
        title="UID"
        :value="appUserInfo.uid"
        center
      />
      <Cell
        title="用户名"
        :value="appUserInfo.userName"
        center
      />
      <Cell
        title="注册时间"
        :value="appUserInfo.createdAt"
        center
      />
    </CellGroup>
    <CellGroup title="信息修改">
      <Cell
        title="修改昵称"
        is-link
        center
        @click="togglePopupNickname"
      />
      <Cell
        title="修改密码"
        is-link
        center
        @click="togglePopupPassword"
      />
    </CellGroup>
    <CellGroup title="数据同步">
      <Cell
        title="上传数据"
        value="上传本地数据至云端"
        is-link
        center
        @click="uploadUserData"
      />
      <Cell
        title="下载数据"
        value="下载云端数据至本地"
        is-link
        center
        @click="downloadUserData"
      />
    </CellGroup>
    <div id="page-settings-main-btns">
      <Button
        id="page-settings-main-btn-unregistration"
        type="danger"
        block
        round
        @click="deleteUserAccount"
      >
        注销账号
      </Button>
      <Button
        id="page-settings-main-btn-logout"
        type="warning"
        block
        round
        @click="requestToLogout"
      >
        退出登录
      </Button>
    </div>
    <PageSettingsNickname
      :show.sync="showPopupNickname"
    />
    <PageSettingsPassword
      :show.sync="showPopupPassword"
    />
  </div>
</template>

<style scoped>
#page-settings {
  height: 100%;
}

#page-settings-main-icon {
  display: block;
  margin: 16px auto;
}

#page-settings-main-nickname {
  display: block;
  margin: 0;
  font-size: 16px;
  font-weight: normal;
  text-align: center;
  color: var(--primary-color);
}

#page-settings-main-btns {
  padding: 32px 16px;
}

#page-settings-main-btn-logout {
  margin-top: 16px;
}

#page-settings-main-btn-retry {
  position: fixed;
  right: 30px;
  bottom: 30px;
}
</style>

<script>
import {
  baseURLImg,
  urlAPIDeleteUserUnregistration,
  urlAPIGetUserLogout,
  urlAPIPostUserBookmark,
  urlAPIPostUserBackground,
  urlAPIGetUserBookmark,
  urlAPIGetUserBackground
} from '@/api/requestURLs.js'
import { AESCBCEncryptor } from '@/utils/AESCBCCryptor.js'
import { axiosRenewAccessToken, axiosDecryptWithAESCBC } from '@/utils/axiosInterceptor.js'
import { Button, Cell, CellGroup, Dialog, Image as VanImage, NavBar, Notify } from 'vant'
import PageSettingsPassword from '@/components/PageSettingsPassword.vue'
import PageSettingsNickname from '@/components/PageSettingsNickname.vue'

export default {
  name: 'PageSettings',
  components: {
    Button,
    Cell,
    CellGroup,
    NavBar,
    VanImage,
    PageSettingsNickname,
    PageSettingsPassword
  },
  data () {
    return {
      isDeletingUserAccount: false,
      isLoggingOut: false,
      isUploadingUserData: false,
      isDownloadingUserData: false,
      urls: {
        urlDeleteAccount: urlAPIDeleteUserUnregistration,
        urlGetUserLogout: urlAPIGetUserLogout,
        urlPostUserBookmark: urlAPIPostUserBookmark,
        urlPostUserBackground: urlAPIPostUserBackground,
        urlGetUserBookmark: urlAPIGetUserBookmark,
        urlGetUserBackground: urlAPIGetUserBackground
      },
      showPopupPassword: false,
      showPopupNickname: false
    }
  },
  computed: {
    appUserInfo () {
      return this.$store.state.appUserInfo
    },
    isConnected () {
      return this.$store.getters.isConnected
    },
    isLoggedIn () {
      return this.$store.getters.isLoggedIn
    }
  },
  watch: {
    isConnected (value) {
      value || this.notifyBadConnection()
    },
    isLoggedIn (value) {
      if (value) return
      this.$router.replace('/login')
      this.notifyLoginAgain()
    }
  },
  methods: {
    // 跳转到上一页
    jumpToPreviousPage () {
      this.$router.back()
    },
    // 显示修改昵称弹出层
    togglePopupNickname () {
      this.showPopupNickname = true
    },
    // 显示修改密码弹出层
    togglePopupPassword () {
      this.showPopupPassword = true
    },
    // 显示服务器连接失败通知
    notifyBadConnection () {
      Notify({
        type: 'danger',
        message: '连接服务器失败'
      })
    },
    // 显示重新登录通知
    notifyLoginAgain () {
      Notify({
        type: 'warning',
        message: '请重新登录'
      })
    },
    // 显示请求频率过高通知
    notifyRequestFrequencyHigh () {
      Notify({
        type: 'warning',
        message: '请求过于频繁，请稍后重试'
      })
    },
    // 清除本地相关数据
    clearLocalDate () {
      // 清除访问令牌
      this.$store.dispatch('updateAppJWTString', undefined)
      // 清除用户信息
      this.$store.dispatch('updateAppUserInfo', undefined)
    },
    // 注销账号
    async deleteUserAccount () {
      // 防止多次连续请求
      if (this.isDeletingUserAccount) {
        return
      }
      this.isDeletingUserAccount = true
      // 再次确认是否删除账户
      const confirmed = await Dialog.confirm({
        title: '警告',
        message: '请再次确认是否注销账户'
      }).then(() => true)
        .catch(() => false)
      if (!confirmed) {
        return
      }
      // 未成功连接服务器
      if (!this.isConnected) {
        this.notifyBadConnection()
        return
      }
      // 向后端请求注销账户
      axiosDecryptWithAESCBC().delete(this.urls.urlDeleteAccount)
        .then(() => {
          // 注销成功的通知
          Notify({
            type: 'success',
            message: '注销成功'
          })
          // 清除本地数据
          this.clearLocalDate()
          // 跳转到首页
          this.$router.replace('/')
        })
        .catch(() => {
          // 账号注销失败的通知
          this.isConnected && this.isLoggedIn && Notify({
            type: 'danger',
            message: '账号注销失败'
          })
        })
        .finally(() => {
          this.isDeletingUserAccount = false
        })
    },
    // 退出登录
    requestToLogout () {
      // 防止多次连续请求
      if (this.isLoggingOut) {
        return
      }
      this.isLoggingOut = true
      // 未成功连接服务器
      if (!this.isConnected) {
        this.notifyBadConnection()
        return
      }
      // 像后端请求退出登录
      axiosDecryptWithAESCBC().get(this.urls.urlGetUserLogout)
        .then(() => {
          // 登出成功的通知
          Notify({
            type: 'success',
            message: '退出登录成功'
          })
          // 清除本地数据
          this.clearLocalDate()
          // 跳转到首页
          this.$router.replace('/')
        })
        .catch(() => {
          // 退出登录失败的通知
          this.isConnected && this.isLoggedIn && Notify({
            type: 'danger',
            message: '退出登录失败'
          })
        })
        .finally(() => {
          this.isLoggingOut = false
        })
    },
    // 上传用户书签信息
    postUserBookmarks () {
      // 自定义标识符
      const aesSecrets = this.$store.state.appAESSecrets
      const bookmarks = this.$store.state.appUserBookmarks
      const formData = new FormData()
      // 用户书签详情
      const detail = bookmarks.map(item => ({
        title: item?.title,
        url: item?.url,
        iconName: item.iconName
      }))
      // 加密的书签详情
      const encDetail = AESCBCEncryptor(aesSecrets.iv, aesSecrets.key, detail)
      // 书签图片文件
      const iconFiles = bookmarks.map(item => item.iconFile)
      // 填充表单数据
      formData.append('detail', encDetail)
      iconFiles.forEach(item => {
        formData.append('icon', item)
      })
      // 上传表单数据
      return new Promise((resolve, reject) =>
        axiosRenewAccessToken().post(this.urls.urlPostUserBookmark, formData)
          .then(() => {
            // 上传成功的通知
            Notify({
              type: 'success',
              duration: 2000,
              message: '书签信息上传成功',
              onClose: () => resolve()
            })
          })
          .catch(error => {
            if (!this.isConnected) return reject(new Error('server disconnected'))
            if (!this.isLoggedIn) return reject(new Error('user is not logged in'))
            // 上传失败的通知
            console.error(error)
            Notify({
              type: 'danger',
              duration: 2000,
              message: '书签信息上传失败',
              onClose: () => resolve()
            })
          }))
    },
    // 上传用户背景信息
    postUserBackground () {
      // 自定义标识符
      const aesSecrets = this.$store.state.appAESSecrets
      const background = this.$store.state.appUserBackground
      const formData = new FormData()
      // 用户背景详情
      const detail = {
        titleColor: background.bmTitleColor,
        imageName: background.bgImgName
      }
      // 加密的背景详情
      const encDetail = AESCBCEncryptor(aesSecrets.iv, aesSecrets.key, detail)
      // 填充表单数据
      formData.append('detail', encDetail)
      background?.bgImgFile && formData.append('image', background.bgImgFile)
      // 上传表单数据
      return new Promise((resolve, reject) =>
        axiosRenewAccessToken().post(this.urls.urlPostUserBackground, formData)
          .then(() => {
            // 上传成功的通知
            Notify({
              type: 'success',
              duration: 2000,
              message: '背景信息上传成功',
              onClose: () => resolve()
            })
          })
          .catch(error => {
            if (!this.isConnected) return reject(new Error('server disconnected'))
            if (!this.isLoggedIn) return reject(new Error('user is not logged in'))
            // 上传失败的通知
            console.error(error)
            Notify({
              type: 'danger',
              duration: 2000,
              message: '背景信息上传失败',
              onClose: () => resolve()
            })
          }))
    },
    // 下载用户书签信息
    async downloadUserBookmarks () {
      // 响应书签信息数组
      const resBms = []
      // 临时书签信息数组
      const bookmarks = []
      // 等待获取用户书签详情
      const tempBms = await new Promise((resolve, reject) =>
        axiosDecryptWithAESCBC().get(this.urls.urlGetUserBookmark)
          .then(response => {
            return resolve(response?.data?.payload)
          })
          .catch(error => {
            if (!this.isConnected) return reject(new Error('server disconnected'))
            if (!this.isLoggedIn) return reject(new Error('user is not logged in'))
            // 下载失败的通知
            console.error(error)
            Notify({
              type: 'danger',
              duration: 2000,
              message: '书签信息下载失败',
              onClose: () => reject(error)
            })
          }))
      resBms.push(...tempBms)
      // 逐个获取书签图标数据
      for (const item of resBms) {
        // 单个书签信息
        const iconURL = `${baseURLImg}${item?.iconPath}`
        const iconName = item?.iconPath?.split('/').pop()
        const iconType = `image/${item?.iconPath?.split('.').pop()}`
        const bookmark = {
          title: item?.title,
          url: item?.url,
          iconName: iconName,
          iconSrc: undefined,
          iconFile: undefined
        }
        // 获取书签图标
        await axiosRenewAccessToken().get(iconURL, {
          responseType: 'blob'
        })
          // 书签信息获取成功后，获取并转换图标文件
          .then(response => new Promise((resolve, reject) => {
            const iconBlob = response?.data
            if (!iconBlob?.size) {
              return reject(new Error(`invalid icon file ${iconName}`))
            }
            const reader = new FileReader()
            // 补全单个书签信息
            reader.addEventListener('load', () => {
              bookmark.iconSrc = reader.result
              bookmark.iconFile = new File([iconBlob], iconName, {
                type: iconType
              })
              return resolve()
            })
            reader.addEventListener('error', () => {
              return reject(new Error(`failed to convert icon file ${iconName}`))
            })
            reader.addEventListener('abort', () => {
              return reject(new Error(`icon file ${iconName} conversion aborted`))
            })
            reader.readAsDataURL(iconBlob)
          }).catch(error => {
            console.error(error)
          }).finally(() => {
            // 填充临时书签信息数组
            bookmarks.push(bookmark)
          }))
          .catch(() => {
            // 书签图标获取失败
            console.log(`failed to access image from ${iconURL}`)
            // 填充临时书签信息数组
            bookmarks.push(bookmark)
          })
      }
      // 保存临时书签信息至本地
      return new Promise((resolve, reject) =>
        this.$store.dispatch('saveAppUserBookmarks', bookmarks)
          .then(() => {
            Notify({
              type: 'success',
              duration: 2000,
              message: '书签信息更新完成',
              onClose: () => resolve()
            })
          })
          .catch(error => {
            Notify({
              type: 'danger',
              duration: 2000,
              message: '书签信息更新失败',
              onClose: () => reject(error)
            })
          }))
    },
    // 下载用户背景信息
    async downloadUserBackground () {
      // 等待获取用户背景详情
      const resBg = await new Promise((resolve, reject) =>
        axiosDecryptWithAESCBC().get(this.urls.urlGetUserBackground)
          .then(response => {
            return resolve(response?.data?.payload)
          })
          .catch(error => {
            if (!this.isConnected) return reject(new Error('server disconnected'))
            if (!this.isLoggedIn) return reject(new Error('user is not logged in'))
            // 下载失败的通知
            Notify({
              type: 'danger',
              duration: 2000,
              message: '背景信息下载失败',
              onClose: () => reject(error)
            })
          }))
      // 自定义标识符
      const imgURL = `${baseURLImg}${resBg?.imagePath}`
      const imgName = resBg?.imagePath.split('/').pop()
      const imgType = `image/${resBg?.imagePath.split('.').pop()}`
      // 获取背景图片数据
      const imgData = await new Promise((resolve, reject) =>
        axiosRenewAccessToken().get(imgURL, {
          responseType: 'blob'
        }).then(response => {
          const imgBlob = response?.data
          if (!imgBlob?.size) {
            return reject(new Error(`invalid image file ${imgName}`))
          }
          const reader = new FileReader()
          reader.addEventListener('load', () => {
            const tempImgData = {
              bgImgDataURL: reader.result,
              bgImgFile: new File([imgBlob], imgName, {
                type: imgType
              })
            }
            return resolve(tempImgData)
          })
          reader.addEventListener('error', () => {
            return reject(new Error(`failed to convert image file ${imgName}`))
          })
          reader.addEventListener('abort', () => {
            return reject(new Error(`image file ${imgName} conversion aborted`))
          })
          reader.readAsDataURL(imgBlob)
        }).catch(() => {
          if (!this.isConnected) return reject(new Error('server disconnected'))
          if (!this.isLoggedIn) return reject(new Error('user is not logged in'))
          // 下载失败的通知
          console.log(`failed to access image from ${imgURL}`)
          Notify({
            type: 'danger',
            duration: 2000,
            message: '背景信息下载失败',
            onClose: () => resolve({
              bgImgDataURL: undefined,
              bgImgFile: undefined
            })
          })
        }))
        .catch(error => {
          console.error(error)
        })
      // 完整背景信息
      const bgInfo = {
        bmTitleColor: resBg?.titleColor,
        bgImgName: imgName,
        bgImgFile: imgData?.bgImgFile,
        bgImgDataURL: imgData?.bgImgDataURL
      }
      // 保存临时背景信息至本地
      return new Promise((resolve, reject) =>
        this.$store.dispatch('saveAppUserBackground', bgInfo)
          .then(() => {
            Notify({
              type: 'success',
              duration: 2000,
              message: '背景信息更新完成',
              onClose: () => resolve()
            })
          })
          .catch(error => {
            Notify({
              type: 'danger',
              duration: 2000,
              message: '背景信息更新失败',
              onClose: () => reject(error)
            })
          }))
    },
    // 上传用户数据
    async uploadUserData () {
      // 防止多次连续请求
      if (this.isUploadingUserData) {
        this.notifyRequestFrequencyHigh()
        return
      }
      this.isUploadingUserData = true
      try {
        // 上传用户书签信息
        await this.postUserBookmarks()
        // 上传用户背景信息
        await this.postUserBackground()
      } finally {
        // 10秒后解除请求限制
        setTimeout(() => {
          this.isUploadingUserData = false
        }, 10000)
      }
    },
    // 下载用户数据
    async downloadUserData () {
      // 防止多次连续请求
      if (this.isDownloadingUserData) {
        this.notifyRequestFrequencyHigh()
        return
      }
      this.isDownloadingUserData = true
      try {
        // 下载用户书签信息
        await this.downloadUserBookmarks()
        // 下载用户背景信息
        await this.downloadUserBackground()
      } finally {
        // 10秒后解除请求限制
        setTimeout(() => {
          this.isDownloadingUserData = false
        }, 10000)
      }
    }
  }
}
</script>
