<template>
  <Popup
    id="page-settings-password"
    v-model="showPopup"
    position="bottom"
    closeable
    safe-area-inset-bottom
    :close-on-click-overlay="false"
    @open="onPopupOpen"
    @close="onPopupClose"
  >
    <div
      id="page-settings-password-title"
      class="van-hairline--bottom"
    >
      修改密码
    </div>
    <Form
      id="page-settings-password-form"
      ref="pageSettingsPasswordFrom"
      :show-error="false"
      @submit="doSubmitFormValues"
    >
      <Field
        ref="fieldOldPassword"
        v-model="formValues.oldPassword"
        name="oldPassword"
        type="password"
        label="旧密码"
        placeholder="请输入8至20个字符（字母或数字）"
        :rules="rulesOldPassword"
      />
      <Field
        ref="fieldNewPassword"
        v-model="formValues.newPassword"
        name="newPassword"
        type="password"
        label="新密码"
        placeholder="请输入8至20个字符（字母或数字）"
        :rules="rulesNewPassword"
      />
      <Field
        ref="fieldConfirmation"
        v-model="formValues.confirmation"
        name="confirmation"
        type="password"
        label="密码确认"
        placeholder="请再次输入密码"
        :rules="rulesConfirmation"
      />
      <div id="page-settings-password-btns">
        <Button
          round
          block
          type="info"
          native-type="submit"
        >
          确认修改
        </Button>
      </div>
    </Form>
  </Popup>
</template>

<style scoped>
#page-settings-password-title {
  font-size: 16px;
  line-height: 54px;
  text-align: center;
}

#page-settings-password-btns {
  padding: 32px 16px;
}
</style>

<script>
import { SHA256Calculator } from '@/utils/hashCalculator.js'
import { axiosRenewAccessToken, axiosEncryptWithRSAOAEP } from '@/utils/axiosInterceptor.js'
import { urlAPIGetRSACryptionKey, urlAPIPatchUserPassword } from '@/api/requestURLs.js'
import { Button, Field, Form, Notify, Popup } from 'vant'

export default {
  name: 'PageSettingsPassword',
  components: {
    Button,
    Field,
    Form,
    Popup
  },
  props: {
    show: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      patchingPassword: false,
      urls: {
        getRSAPubKey: urlAPIGetRSACryptionKey,
        patchPassword: urlAPIPatchUserPassword
      },
      formValues: {
        oldPassword: '',
        newPassword: '',
        confirmation: ''
      },
      rulesOldPassword: [
        {
          required: true,
          message: '请填写密码'
        },
        {
          message: '',
          validator: this.checkPasswordFormat
        }
      ],
      rulesNewPassword: [
        {
          required: true,
          message: '请填写密码'
        },
        {
          message: '',
          validator: this.checkPasswordFormat
        },
        {
          message: '新密码应与旧密码不同',
          validator: this.distinguishPassword
        }
      ],
      rulesConfirmation: [
        {
          required: true,
          message: '请再次填写新密码'
        },
        {
          message: '两次填写的新密码不一致',
          validator: this.confirmPassword
        }
      ]
    }
  },
  computed: {
    showPopup: {
      get () {
        return this.show
      },
      set (value) {
        this.$emit('update:show', value)
      }
    },
    isConnected () {
      return this.$store.getters.isConnected
    },
    isLoggedIn () {
      return this.$store.getters.isLoggedIn
    }
  },
  methods: {
    // 检查密码格式是否正确
    checkPasswordFormat (value, rule) {
      const pattern = /^[a-z0-9]+$/i
      let checkPassed = false
      if (!pattern.test(value)) {
        rule.message = '不应包含除字母和数字以外的字符'
      } else if (value.length < 8) {
        rule.message = '字符不应少于8个'
      } else if (value.length > 20) {
        rule.message = '字符不应多于20个'
      } else {
        checkPassed = true
      }
      return checkPassed
    },
    // 检查新旧密码是否相同
    distinguishPassword (value) {
      return this.formValues.oldPassword !== value
    },
    // 检查两次填写的新密码是否相同
    confirmPassword (value) {
      return this.formValues.newPassword === value
    },
    // 响应弹出层打开事件
    onPopupOpen () {
      // 重置表单数据
      Object.assign(this.formValues, {
        oldPassword: '',
        newPassword: '',
        confirmation: ''
      })
    },
    // 重置表单数据
    onPopupClose () {
      // 重置表单校验
      this.$refs.pageSettingsPasswordFrom.resetValidation()
    },
    // 显示服务器连接失败通知
    notifyBadConnection () {
      Notify({
        type: 'danger',
        message: '连接服务器失败'
      })
    },
    // 从后端获取RSA-OAEP公钥
    getRSAPubKey () {
      return axiosRenewAccessToken().get(this.urls.getRSAPubKey)
        .then(response => response.data.payload.key)
        .catch(error => Promise.reject(error))
    },
    // 前端向后端发送加密后的信息
    patchRSAEncryptedMsg (rsaPubKey, message) {
      return axiosEncryptWithRSAOAEP(rsaPubKey).patch(this.urls.patchPassword, message)
        .then(response => response)
        .catch(error => {
          const resMsg = error?.response?.data?.message
          if (resMsg === 'crypto/bcrypt: hashedPassword is not the hash of the given password') {
            this.$refs.fieldOldPassword.validateMessage = '输入的旧密码不正确'
          }
          if (resMsg?.includes('RequestUserPasswordChange.newPassword')) {
            this.$refs.fieldNewPassword.validateMessage = '请再次检查新密码'
          }
          return Promise.reject(error)
        })
    },
    // 异步提交表单数据
    async submitFormValues (values) {
      // 从后端获取RSA-OAEP公钥
      const rsaPubKey = await this.getRSAPubKey()
      // 加密相关表单数据
      const message = {
        oldPassword: SHA256Calculator(values.oldPassword),
        newPassword: SHA256Calculator(values.newPassword)
      }
      // 前端向后端发送加密后的信息
      const response = await this.patchRSAEncryptedMsg(rsaPubKey, message)
      // 返回响应结果
      return response
    },
    // 执行异步提交表单数据
    doSubmitFormValues (values) {
      // 防止连续多次请求
      if (this.patchingPassword) {
        return
      } else {
        this.patchingPassword = true
      }
      // 未成功连接服务器前不执行后续操作
      if (!this.isConnected) {
        this.notifyBadConnection()
        return
      }
      // 在连接服务器的前提下，提交表单数据
      this.submitFormValues(values)
        .then(response => {
          // 修改成功的通知
          Notify({
            type: 'success',
            message: '密码修改成功'
          })
          // 关闭弹出层
          this.showPopup = false
          // 退出登录，清除本地数据
          Promise.allSettled([
            // 清除访问令牌
            this.$store.dispatch('updateAppJWTString', undefined),
            // 清除用户信息
            this.$store.dispatch('updateAppUserInfo', undefined)
          ]).then(() => {
            // 跳转到登录页
            this.$router.replace('/login')
          })
        })
        .catch(() => {
          // 密码修改失败的通知
          this.isConnected && this.isLoggedIn && Notify({
            type: 'danger',
            message: '密码修改失败'
          })
        })
        .finally(() => {
          this.patchingPassword = false
        })
    }
  }
}
</script>
