<template>
  <Popup
    id="page-settings-nickname"
    v-model="showPopup"
    position="bottom"
    closeable
    safe-area-inset-bottom
    :close-on-click-overlay="false"
    @open="onPopupOpen"
    @close="onPopupClose"
  >
    <div
      id="page-settings-nickname-title"
      class="van-hairline--bottom"
    >
      修改昵称
    </div>
    <Form
      id="page-settings-nickname-form"
      ref="pageSettingsNicknameForm"
      :show-error="false"
      @submit="doSubmitFormValues"
    >
      <Field
        ref="fieldOldNickname"
        v-model="formValues.oldNickname"
        name="oldNickname"
        label="旧昵称"
        placeholder="请输入1至30个字符（字母/数字/汉字）"
        :rules="rulesOldNickname"
        disabled
      />
      <Field
        ref="fieldNewNickname"
        v-model="formValues.newNickname"
        name="newNickname"
        label="新昵称"
        placeholder="请输入1至30个字符（字母/数字/汉字）"
        :rules="rulesNewNickname"
      />
      <div id="page-settings-nickname-btns">
        <Button
          round
          block
          type="info"
          native-type="submit"
        >
          确认修改
        </Button>
      </div>
    </Form>
  </Popup>
</template>

<style scoped>
#page-settings-nickname-title {
  font-size: 16px;
  line-height: 54px;
  text-align: center;
}

#page-settings-nickname-btns {
  padding: 32px 16px;
}
</style>

<script>
import { axiosEncryptWithAESCBC } from '@/utils/axiosInterceptor.js'
import { urlAPIPatchUserNickname } from '@/api/requestURLs.js'
import { Button, Field, Form, Notify, Popup } from 'vant'

export default {
  name: 'PageSettingsNickname',
  components: {
    Button,
    Field,
    Form,
    Popup
  },
  props: {
    show: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      patchingNickname: false,
      urls: {
        patchNickname: urlAPIPatchUserNickname
      },
      formValues: {
        oldNickname: '',
        newNickname: ''
      },
      rulesOldNickname: [
        {
          required: true,
          message: '请填写旧昵称'
        },
        {
          message: '',
          validator: this.checkNicknameFormat
        }
      ],
      rulesNewNickname: [
        {
          required: true,
          message: '请填写新昵称'
        },
        {
          message: '',
          validator: this.checkNicknameFormat
        },
        {
          message: '新昵称应与旧昵称不同',
          validator: this.distinguishNickname
        }
      ]
    }
  },
  computed: {
    showPopup: {
      get () {
        return this.show
      },
      set (value) {
        this.$emit('update:show', value)
      }
    },
    isConnected () {
      return this.$store.getters.isConnected
    },
    isLoggedIn () {
      return this.$store.getters.isLoggedIn
    }
  },
  methods: {
    // 检查昵称格式
    checkNicknameFormat (value, rule) {
      const pattern = /^[\u4e00-\u9fa5\w]+$/
      let checkPassed = false
      if (!pattern.test(value)) {
        rule.message = '只能使用：字母/数字/汉字'
      } else if (value.length < 1) {
        rule.message = '字符不应少于1个'
      } else if (value.length > 30) {
        rule.message = '字符不应多于30个'
      } else {
        checkPassed = true
      }
      return checkPassed
    },
    // 检查新旧昵称是否相同
    distinguishNickname (value) {
      return this.formValues.oldNickname !== value
    },
    // 响应弹出层显示事件
    onPopupOpen () {
      // 重置表单数据
      Object.assign(this.formValues, {
        oldNickname: this.$store.state.appUserInfo.nickName,
        newNickname: ''
      })
    },
    // 重置表单状态
    onPopupClose () {
      // 重置表单校验
      this.$refs.pageSettingsNicknameForm.resetValidation()
    },
    // 显示服务器连接失败通知
    notifyBadConnection () {
      Notify({
        type: 'danger',
        message: '连接服务器失败'
      })
    },
    // 前端向后端发送加密后的信息
    patchAESEncryptedMsg (message) {
      return axiosEncryptWithAESCBC().patch(this.urls.patchNickname, message)
        .then(response => response)
        .catch(error => {
          const resMsg = error?.response?.data?.message
          if (resMsg === 'mismatched old nickname') {
            this.$refs.fieldOldNickname.validateMessage = '旧昵称不匹配'
          }
          if (resMsg?.includes('RequestUserNicknameChange.oldNickname')) {
            this.$refs.fieldOldNickname.validateMessage = '请再次检查旧昵称'
          }
          if (resMsg?.includes('RequestUserNicknameChange.newNickname')) {
            this.$refs.fieldNewNickname.validateMessage = '请再次检查新昵称'
          }
          return Promise.reject(error)
        })
    },
    // 异步提交表单数据
    async submitFormValues (values) {
      const message = {
        oldNickname: this.formValues.oldNickname,
        newNickname: this.formValues.newNickname
      }
      const response = await this.patchAESEncryptedMsg(message)
      return response
    },
    // 执行异步提交表单数据
    doSubmitFormValues (values) {
      // 防止连续多次请求
      if (this.patchingNickname) {
        return
      } else {
        this.patchingNickname = true
      }
      // 未成功连接服务器前不执行后续操作
      if (!this.isConnected) {
        this.notifyBadConnection()
        return
      }
      // 在连接服务器的前提下，提交表单数据
      this.submitFormValues(values)
        .then(() => {
          // 修改成功的通知
          Notify({
            type: 'success',
            message: '昵称修改成功'
          })
          // 保存昵称至本地
          this.$store.dispatch('updateAppUserInfo', {
            nickName: this.formValues.newNickname
          }).then(() => {
            // 关闭弹出层
            this.showPopup = false
          })
        })
        .catch(() => {
          // 昵称修改失败的通知
          this.isConnected && this.isLoggedIn && Notify({
            type: 'danger',
            message: '昵称修改失败'
          })
        })
        .finally(() => {
          this.patchingNickname = false
        })
    }
  }
}
</script>
